.no-page-ctn{
    display: flex;
    justify-content: center;
}    
    
.no-page{
   background-color: rgba(91, 198, 198, 0.7);
   color:rgb(7, 112, 112);
   border: 3px solid;
   border-radius: 10px;
   height: 30em;
   width: 60%;
   padding: 0.6rem;
} 

.no-page h1{
    text-align: center;   
} 

span.red{
  color:red;
}

.emoji{
   margin-top:5rem;
   margin-bottom: 2rem;
   font-size: 4rem;
   text-align: center;
   text-shadow: 0px 0px 3px black;
}

@media all and (max-width:800px) {
    .no-page{
      width: 80%;
    }
}   
  